body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgba(128, 0, 128, 0.605);
  text-align: center;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 25.25rem;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  padding: 1.25rem;
  background: white;
  border-radius: 10px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.495);
  text-align: left;
  
}
.heading {
  text-align: center;
  font-size:1.563rem ;
  
}
.btn {
  background: rgba(128, 0, 128, 0.605);
  width: 100%;
  padding: 9px;
  border: none;
  margin-top: 15px;
  color:white;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-top: 10px;

}
.flex_box {
  display: flex;
  align-items: center;
}
.text_align {
  padding-left: 10px;
  line-height: 5px;
  
}